import { Component } from "react";
import './components.css'


class Test extends Component {

    state = {
        visibility: false,
        visibility1:false,
    }

    
    render() {
        var toggleState = () =>{
            this.setState({visibility:!this.state.visibility})
            console.log("1");
        }
        var toggleState2 = () =>{
            this.setState({visibility1:!this.state.visibility1})
            console.log(this.state.visibility1);
        }
        const x = this.state.visibility;
        const y = this.state.visibility1;

        
        return(
            <>
            <h2>Styling using Functional and Class Components</h2>
            <button className="button1" onClick = {toggleState}>To see styling in class component</button>
            
            <button className="button2" onClick = {toggleState2}>To see styling in functional component</button>
            {
                x && (<div className><ClassComponent /></div>)
            }
            {
                y && (<div className><FunctionalComponent /></div>)
            }
            </>
        )
    }
}


function FunctionalComponent() {
    return(
        <div className = "div1">
            <h3>This is created using functional Component</h3>
            <p className ="para1">This is done using external CSS</p>
            <p style = {{color:"blue"}}>This is done using inline CSS</p>

        </div>
    )
}

class ClassComponent extends Component {
    render(){
        return(
            <div className = "div2">
            <h3>This is created using class Component</h3>
            <p className ="para1">This is done using external CSS</p>
            <p style = {{color:"blue"}}>This is done using inline CSS</p>

        </div> 
        )
    }
}
export default Test;